import React from "react";
import styled from "styled-components";
import { Grid, Text, Input } from "../elements";
import { useState } from "react";
import theme from "../styles/theme";
import logger from "../shared/Console";

const UploadInput = (props) => {
  const { color, fontSize } = theme;

  React.useEffect(() => {
    logger("uploadinput ", props);
  }, []);

  const [post_info, setPostInfo] = useState(
    props.post_info !== {}
      ? {
          place: props.post_info.place,
          restaurant: props.post_info.restaurant,
          headCount: props.post_info.headCount,
          appointmentTime: props.post_info.appointmentTime,
          foodCategory: props.post_info.foodCategory,
        }
      : {
          place: "",
          restaurant: "",
          headCount: "",
          appointmentTime: "",
          foodCategory: "",
        }
  );

  return (
    <React.Fragment>
      <Grid padding="0 2rem">
        <Container>
          <Grid>
            <FocusWithin>
              <Text
                padding="2.4rem 0 0.8rem"
                color={color.bg80}
                bold="700"
                size={fontSize.base}
              >
                 
              </Text>
              <Input
                border="1px solid #C7C8CE"
                padding="1.5rem 1.3rem"
                size={fontSize.base}
                color={color.bg60}
                placeholder="  ."
                value={post_info.place}
                _onChange={(e) => {
                  setPostInfo({ ...post_info, place: e.target.value });
                  props.onChange({ place: e.target.value });
                }}
              ></Input>
            </FocusWithin>
          </Grid>
          <Grid>
            <FocusWithin>
              <Text
                padding="2.4rem 0 0.8rem"
                color="#888E95"
                bold="700"
                size={fontSize.base}
              >
                  
              </Text>
              <Input
                border="1px solid #C7C8CE"
                padding="1.5rem 1.3rem"
                size={fontSize.base}
                color={color.bg60}
                placeholder="   ."
                value={post_info.restaurant}
                _onChange={(e) => {
                  setPostInfo({ ...post_info, restaurant: e.target.value });
                  props.onChange({ restaurant: e.target.value });
                }}
              ></Input>
            </FocusWithin>
          </Grid>

          <FocusWithin>
            <Text
              padding="2.4rem 0 0.8rem"
              color="#888E95"
              bold="700"
              size={fontSize.base}
            >
                
            </Text>
            <Grid
              border="1px solid #C7C8CE"
              padding="0 1.3rem"
              radius="1.2rem"
              height="auto"
            >
              <Select
                value={`${post_info.headCount}`}
                onChange={(e) => {
                  setPostInfo({
                    ...post_info,
                    headCount: e.target.value,
                  });
                  props.onChange({ headCount: e.target.value });
                }}
              >
                <option value="none" hidden defaultValue>
                   .
                </option>
                <option value="2">2</option>
                <option value="3">3</option>
                <option value="4">4</option>
              </Select>
            </Grid>
            <Text color="red" size={fontSize.tiny} padding="0.5rem 1rem 0">
              5     .
            </Text>
            <Grid></Grid>
          </FocusWithin>
          <Grid>
            <FocusWithin>
              <Text
                padding="2.4rem 0 0.8rem"
                color="#888E95"
                bold="700"
                size={fontSize.base}
              >
                  
              </Text>
              <Input
                type="time"
                border="1px solid #C7C8CE"
                padding="1.5rem 1.3rem"
                size={fontSize.base}
                color={color.bg60}
                value={post_info.appointmentTime}
                _onChange={(e) => {
                  setPostInfo({
                    ...post_info,
                    appointmentTime: e.target.value,
                  });
                  props.onChange({ appointmentTime: e.target.value });
                }}
              ></Input>
            </FocusWithin>
          </Grid>
          <FocusWithin>
            <Grid margin="0 auto 1rem">
              <Text
                padding="2.4rem 0 0.8rem"
                color="#888E95"
                bold="700"
                size={fontSize.base}
              >
                 
              </Text>
              <Grid
                border="1px solid #C7C8CE"
                padding="0 1.3rem"
                radius="1.2rem"
                height="auto"
              >
                <Select
                  value={post_info.foodCategory}
                  onChange={(e) => {
                    setPostInfo({
                      ...post_info,
                      foodCategory: e.target.value,
                    });
                    props.onChange({ foodCategory: e.target.value });
                  }}
                >
                  <option value="none" defaultValue hidden>
                      .
                  </option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                </Select>
              </Grid>
            </Grid>
          </FocusWithin>
        </Container>
      </Grid>
    </React.Fragment>
  );
};

const Container = styled.div`
  display: flex;
  flex-direction: column;
`;

const Select = styled.select`
  width: 100%;
  height: 4.7rem;
  border: none;
  padding: 0;
  font-size: 1.4rem;
  &:focus {
    outline: none;
  }
`;

const FocusWithin = styled.div`
  &:focus-within p {
    color: #ff9425;
  }
  &:focus-within input {
    border: 1px solid #ff9425;
    outline: none;
  }
`;

export default UploadInput;
