import { createAction, handleActions } from 'redux-actions';
import { produce } from 'immer';
import axiosModule from '../axios_module';
import jwtDecode from 'jwt-decode';

//  console.log() 
import logger from '../../shared/Console';

// token
import { token } from '../../shared/OAuth';

// Action
const SET_USER = 'SET_USER';
const LOG_OUT = 'LOG_OUT';

// Action Creator
const setUser = createAction(SET_USER, (user_info) => ({ user_info }));
const logOut = createAction(LOG_OUT, () => {});

// Initial State
const initialState = {
  user: null,
  is_login: false,
};

// middleware
const kakaoLogin = (code) => {
  return function (dispatch, getState, { history }) {
    axiosModule
      .get(`user/kakao/callback?code=${code}`)
      .then((res) => {
        //    jwt token 
        logger('user - 36', res);

        const ACCESS_TOKEN = res.data.token;

        //   
        sessionStorage.setItem('token', ACCESS_TOKEN);

        //   user     
        const token = sessionStorage.getItem('token');

        // jwtDecode  user      
        logger('user  decoding', jwtDecode(token));
        const user_nickname = jwtDecode(token).username;
        const user_id = jwtDecode(token).userId;

        dispatch(
          setUser({
            user_id: user_id,
            user_nickname: user_nickname,
          })
        );

        window.alert(`${user_nickname} .`);
        window.location.replace('/home');
      })
      .catch((err) => {
        logger('user  74 -  ', err);
        window.alert(' .');
        history.replace('/'); //    
      });
  };
};

// getUserAX  -  user profile

//     user check    
const loginCheck = () => {
  return function (dispatch, getState, { history }) {
    if (token) {
      const user_nickname = jwtDecode(token).username;
      const user_id = jwtDecode(token).userId;
      dispatch(
        setUser({
          user_id: user_id,
          user_nickname: user_nickname,
        })
      );
    } else {
      dispatch(logOut());
    }
  };
};

// Reducer
export default handleActions(
  {
    [SET_USER]: (state, action) =>
      produce(state, (draft) => {
        draft.user = action.payload.user_info;
        draft.is_login = true;
        logger('set_user ', draft.user);
      }),
    [LOG_OUT]: (state, action) =>
      produce(state, (draft) => {
        sessionStorage.removeItem('token');
        draft.user = null;
        draft.is_login = false;

        window.location.replace('/home');
        window.alert(' .');
      }),
  },
  initialState
);

const actionCreators = {
  kakaoLogin,
  loginCheck,
  logOut,
};

export { actionCreators };
